import React , { useState } from "react";
import './Product.css';
import {ProductData} from './ProductData.js';

const Product1=({slides,onRouteChange})=>{

    const [current, setCurrent] = useState(0);
    let length = slides.length;
    const nextSlide = () => {
      setCurrent(current === length - 1 ? 0 : current + 1);
    };
    const prevSlide = () => {
      setCurrent(current === 0 ? length - 1 : current - 1);
    };
  
    if (!Array.isArray(slides) || slides.length <= 0) {
      return null;
    }
    
  //   function change() {
  //     document.querySelector(".active").innerHTML=document.querySelector(".preimg");
  // }
  
  // document.querySelectorAll(".preimg").forEach(n => n.addEventListener("click", change));
  

    return(

<section className='about'>
    <div className='about-title'>
        <div className='previews'>
          {
          ProductData.map((preimg, index) => {
          return (
                <div>
                  {
                  index === current 
                  ?<img src={ProductData[index].image} alt='product' className='preimg bo' />
                  :<img src={ProductData[index].image} alt='product' className='preimg ' />
                  }
              </div>
          );
        })
        }
          <button class="sliderup" onClick={prevSlide}>&uarr;</button>
          <button class="sliderdown" onClick={nextSlide}>&darr;</button>
        </div>

    <div className='pbuy'>
    {/* <img className='pre' alt='product' src={p1}/> */}
    {ProductData.map((slide, index) => {
        return (
          <div
            className={index === current ? 'slide active' : 'slide'}
            key={index}
          >
            {index === current && (
              <img src={slide.image} alt='travel' className='pre' />
            )}
          </div>
        );
      })}
    <button class="sliderL" onClick={prevSlide}>&#10094;</button>
    <button class="sliderR"  onClick={nextSlide}>&#10095;</button>
     
    <div className='buy'>
    <div className='b' onClick={()=>onRouteChange('cart')}>Add to cart</div>
    <div className='b'onClick={()=>onRouteChange('cart')}>Buy</div>
    </div>
    </div>
    </div>
    <div className="about-pages">
    <div className='pdetails'>
         <div className='pname'>An Ordinary Pen</div>
         <div className='by'> By ABC</div>
         <div className='RR'>&#9733;&#9733;&#9733;&#9734;&#9734; 256 Rating, 120 Reviews</div>
         <div className='price'>$20/-</div>
         <div className=''>
         <div className='offer'>Offers</div>
         <ul className='offer1'>
             <li>2% off on online payment.</li>
             <li>5% off on abc bank cards.</li>
             <li>free register subscrption</li>
             <li>Free Delevery on oorders above $50/-.</li>
         </ul>
         </div>
         </div>

         <div className='pcolor'>
             <div className='pvarient'>Varients</div>
             <div className='varients'>
            <div className=''>
             <img className='preimg' alt='product' src='https://www.montblanc.com/variants/images/34480784411791124/A/w570.jpg'/>
             <div className=''>Blue</div>
             </div>
             <div className=''>
             <img className='preimg' alt='product' src='https://www.montblanc.com/variants/images/34480784411791124/A/w570.jpg'/>
             <div className=''>Black</div>
             </div>
             <div className=''>
             <img className='preimg' alt='product' src='https://www.montblanc.com/variants/images/34480784411791124/A/w570.jpg'/>
             <div className=''>red</div>
             </div>           
             </div>
         
         </div>


         <div className='pavail'>
         <div className='avail'>Check Availability</div>
         <div className='pinput'>
           <input className='pin' placeholder='enter area pincode' type='text' size='20px'/>
            <input className='psub' type='submit' size='20px'/>  
         </div>
         
         </div>


         <div className='dis'>
         <div className='pdis'>Product Discription Or Details</div>
         <table  height='500' width='90%' className='detail'>
            <tr>
                <th>Model</th>
                <td>13promax</td>
            </tr>
            <tr>
                <th>Features</th>
                <td>It writes</td>
            </tr>
            <tr>
                <th>Color</th>
                <td>Black,Blue,Red etc</td>
            </tr>
            <tr>
                <th>Dimensions</th>
                <td>20x3x20</td>
            </tr>
        </table> 
         </div>

<div>
         <div className='rating'>
             <div className=''>Rating and Reviews</div>
             <div className=''>&#9733;&#9733;&#9733;&#9733;</div>
             <div className='review'>
                <div className='r1'>Riyan </div>
                <div className=''>&#9733;&#9733;&#9733;&#9733;</div>
                <div className='r1'>This pen is very nice you can write your future with ifself.</div>
             </div>
             <div className='review'>
                <div className='r1'>Riyan </div>
                <div className=''>&#9733;&#9733;&#9733;&#9733;</div>
                <div className='r1'>This pen is very nice you can write your future with ifself.</div>
             </div>
             <div className='review'>
                <div className='r1'>Riyan </div>
                <div className=''>&#9733;&#9733;&#9733;&#9733;</div>
                <div className='r1'>This pen is very nice you can write your future with ifself.</div>
             </div>
            </div>
        </div>

        
         <div className='pdis sim'>Similar Product Section</div>

        
    
    </div>
    </section> 
    );
}
export default Product1;